@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');

* {
    margin: 0;
   
}

body {
    font-family: 'Roboto', sans-serif;
}

.top-bar {
    height: 48px;
    width: 100;
    background-color: #fef7f6;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    margin-left: auto;
    margin-right: auto;
}

.contact span:first-child {
    padding-right: 15px;


}

.info span {
    text-align: right;
    padding-right: 15px;
}

.container {
    height: 90.39px;
    width: 100;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
nav.menu{
    font-family: 'Roboto', sans-serif;
    font-size: 13px;
    display: flex;
    gap: 10px;
   a {
    color: black;
    text-decoration: none;

   }
li a {
    padding: 1rem;

   
}

}

.menu a:hover {
   
        color: #ff5353

    
}
.dropdown {
    position: relative;

}
.dropdown ul {
    position: absolute;
    padding: 10px;
    list-style: none;
    text-decoration: none;
    width: 100px;
    display: none;
    
}

.dropdown:hover ul {
    display: block;
}

.dropdown ul li:hover {
    // background-color: ;
}
 .li a {
  padding: 1rem;
  
  &:hover{
  color: #ff5353;
}
 }


.shopping span {
    text-align: right;
    padding-right: 15px;
    font-size: 13px;
    font-weight: 600;
    list-style: none;
    cursor: pointer;
}

.icon-container {
    font-size: 18px;
    display: flex;
    flex-direction: row;

}

.card-bag-icon {
    position: relative;
    padding: 10px;

}

.circle {
    position: absolute;
    background-color: #ff5353;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    top: 0;
    right: 0;
    color: #fff;
    font-size: 8px;
    font-weight: 400;
    line-height: 16px;
    text-align: center;
}

.square {
    background-color: red;
    color: white;
    font-size: 10px;
    font-weight: bold;
    position: relative;
    top: -15px;
    border: 5px solid red;
    border-radius: 15px;
}

.banner {
    height: 530px;
    width: 100%;
    background: url('https://demo.themeies.com/silon/images/bg-1.jpg');
    display: flex;
    justify-content: center;
    // flex-direction: column;
    background-size: cover;

    h2 {
        display: block;
        margin-bottom: 15px;
        font-weight: 500;
        line-height: 0.8;
        letter-spacing: 2.8px;
        text-transform: uppercase;
    }

    h1 {
        font-size: 40px;
        line-height: 56px;
        margin-bottom: 35px;
    }

    button {
        background-color: #ff5353;
        border-radius: 3px;
        color: #fff;
        text-transform: uppercase;
        display: inline-block;
        font-size: 14px;
        font-weight: 500;
        line-height: 0.8;
        min-width: 170px;
        padding: 20px 25px;
    }
}

