.action-links {
    bottom: 60px;
    position: absolute;
    width: 160px;
    // left: 30%;
    bottom: 10px;
    left: calc(50% - 80px);
    visibility: hidden;
    opacity: 1;
    z-index: 10;
    transition: all 300ms ease-in-out;

    a {
        line-height: 50px;
    }
}



.product-image {
    position: relative;
}




.product > div:hover .action-links {
    visibility: visible;
    opacity: 1;

    

    .ti-eye {
        border-radius: 50%;
        width: 50px;
        height: 50px;
        background: white;
        font-size: 14px;
        display: inline-block;
        text-align: center;
    }

    .ti-heart {
        border-radius: 50%;
        width: 50px;
        height: 50px;
        background: white;
        font-size: 14px;
        display: inline-block;
        text-align: center;

    }

    .ti-heart:before {
        content: "\e634";
    }

    *,
    ::after,
    ::before {
        box-sizing: border-box;
    }

    .ti-search {
        border-radius: 50%;
        width: 50px;
        height: 50px;
        background: white;
        font-size: 14px;
        display: inline-block;
        text-align: center;

    }
}

